<template>
  <div id="modal-dialog">
    <div  class="dialog-wapper" :class="{'open': is_open}">
      <div class="overlay" :click="close"></div>
      <div class="dialog">
        <div class="heading">
          <slot name="header">
          </slot>
        </div>
      </div>
      <div>
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'modal-dialog',
  data () {
    return {
      is_open: false
    }
  },
  methods: {
    open () {
      if (!this.is_open) {
        this.$emit('dialogOpen')
      }
      this.is_open = true
    },
    close () {
      if (this.is_open) {
        this.$emit('dialogClose')
      }
      this.is_open = false
    }
  }
}
</script>

<style scoped lang="less">
.dialog-wrapper{
  &.open{
    display: block;
  }
  height: 100%;
  display: none;
  &>.overlay{
    background: rgba(0,0,0,.5);
    z-index: 1;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  &>.dialog{
    position:fixed;
    top:.24rem;
    left:.24rem;
    right:.24rem;
    bottom:.24rem;
    z-index: 20;
    display: none;
    box-shadow: 0 0 .1rem rgba(0, 0, 0, .8);
    & heading {
      padding: .12rem;
    }
  }
}

</style>



